* {
    margin: 0;
    padding: 0;
    font-family: Nunito Sans;
}

.App {
    padding: 0cm 0cm 3cm 0cm;
}

body {
}


.name {
    font-family: Madimi One;
    font-size: 60px;
    line-height: 1.75cm;

    text-align: center;
    // font-weight: 600;
    color: #08a5a8;
}

.buttons {
    margin-top: .5cm;
    display: flex;
    justify-content: center;
    gap: .3cm;
}

.helpline {
    padding: .3cm 1.5cm;
    // width: 100%;
    border-radius: 1000px;
    background-color: #08a5a8;
    border: none;
    color: #e9feff;
    font-size: 16px;
    letter-spacing: .01cm;
    cursor: pointer;
    border: 2.5px solid #08a5a8;
    transition: .3s ease;

}

.hp {
    &:hover {
        background-color: transparent;
    }
}

.donate {
    background-color: transparent;
    // color: black;
    border: 2.5px solid #08a5a8;

    &:hover {
        background-color: #08a5a8;
    }
}

.upper-left {
    width: 12cm;
    display: flex;
    flex-direction: column;
    gap: .1cm;
    // border: thin solid white;
    align-items: center;
}

.upper-right {
    width: 11cm;
    flex-shrink: 0;
}


.yt-video {
    width: 100%;
    aspect-ratio: 16/9;
    border: none;
    border-radius: 10px;
}

.welcome {
    color: #e9feff;
    font-size: 20px;

}

.main-card {
    width: 9cm;
    height: auto;
    border-radius:10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 2px solid #dbfdff ;

}
.head-text{
    font-size: 32px;
    text-align: center;
    // font-weight: 600;
    font-family: dancing script;
}

.lower-div{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1cm;
    margin-top: 1cm;
}
.home-lower{
    padding: 0cm 10vw;
}

.card-upper {
   
    padding: .2cm .5cm 0cm .5cm;
}
.main-card-box{
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-wrap: wrap;
    gap: .5cm;
}
.card-lower{
    display: flex;
    flex-direction: column;
    width: calc(100% - 1cm );
    border-radius: 0px 0px 10px 10px;
    align-items: center;
    // justify-content: center;
    background-color: #e9feff;
    padding: .2cm .5cm;
    height: 100%;
}

.card-lower-head{
    font-size: 30px;
    font-family: Madimi One;
    color: #08a5a8;
    text-align: center;
}

.cardImg {
    width: 100%;
    border-radius: 1000px;
    filter: brightness(60%);
    // border-radius: 10px 10px 0px 0px;
}

.lower-desc{
    text-align: center;
}

.home-upper {
    padding: 3cm 10vw;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &::before {
        position: absolute;
        content: "";
        inset: 0;
        background-image: url("back.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;
        filter: brightness(40%);
    }
}